import 'normalize.css/normalize.css';
import 'styles/App.scss';
import ImgFigure from './ImgFigure.js';
import ControllerUnit from './ControllerUnit.js';
import React from 'react';
import  { findDOMNode } from 'react-dom';
import imageJsonDatas from '../data/imageDatas.json'; /* json */
import { getRangeRandom, get30DegRandom } from '../util/util.js';

/**
 * imageJsonDatasimageDatas
 * webpackloader
 */
const imageDatas = imageJsonDatas.map((image) => {
    image.imageUrl = require('../images/' +  image.fileName);
    return image;
});


class GalleryByReactApp extends React.Component {

    constructor(props) {
        super(props);
        this.Constant = {
            centerPos: {  // 
                left: 0,
                right: 0
            },
            leftSection: {  // xy
                x: [0,0],
                y: [0,0]
            },
            rightSection: { // xy
                x: [0,0],
                y: [0,0]
            },
            topSection: {   // xy
                x: [0, 0],
                y: [0, 0]
            }
        };

        /** imgsArrangeArr  */
        this.state = {
            imgsArrangeArr: [
                /* {
                     pos:{ left:0, top:0}
                     rotate: 0          //
                     isInverse false  // 
                     isCenter: false   //
                }  */
            ]
        };
    }

    /**
     *  
     *  @param: centerIndex
     */
    rearrange(centerIndex) {
        let { imgsArrangeArr } = this.state;
        let { centerPos, leftSection, rightSection, topSection } = this.Constant;

        /**
         * 1. 
         * 2. 
         * 3. imgsArrangeArr(imageDatas)
         */
        let center = imgsArrangeArr.splice(centerIndex, 1);
        center[0] = {
            'pos': centerPos,
            'rotate': 0,
            'isCenter': true
        };

        /**
         * 1. 0150%
         * 2. 0-140-15
         * 3. imgsArrangeArrtopImgNum0, imgsArrangTopArr
         * 4. imgsArrangeArr
         **/
        let top = [];
        let topNum = Math.floor(Math.random() * 2); //
        let topIndex = Math.floor(Math.random() * (imgsArrangeArr.length - topNum));
        top = imgsArrangeArr.splice(topIndex, topNum);

        /**  */
        top.forEach((value, index) => {
            top[index] = {
                pos: {
                    top: getRangeRandom(topSection.y[0], topSection.y[1]),
                    left: getRangeRandom(topSection.x[0], topSection.x[1])
                },
                rotate: get30DegRandom(),
                isCenter: false
            };
        });

        /**  */
        for (let i = 0, j = imgsArrangeArr.length, k = j / 2; i < j; i++) {
            //,,y
            let xRang = i<k ? leftSection.x :rightSection.x;
            imgsArrangeArr[i] = {
                pos: {
                    top: getRangeRandom(leftSection.y[0], leftSection.y[1]),
                    left: getRangeRandom(xRang[0], xRang[1])
                },
                rotate: get30DegRandom(),
                isCenter: false
            };
        }

        /** imgsArrangeArr */
        if (top && top[0]) {
            imgsArrangeArr.splice(topIndex, 0, top[0]);
        }
        /** imgsArrangeArr */
        imgsArrangeArr.splice(centerIndex, 0, center[0]);

        this.setState({ imgsArrangeArr });
    }

    /**
     * rearrangeindex
     * @param index 
     * return {function}
     */
    center(index){
        this.rearrange(index);
    }

    /**
     * 
     * @param index inverseindex
     */
    inverse(index) {
        let { imgsArrangeArr } = this.state;
        imgsArrangeArr[index].isInverse = !imgsArrangeArr[index].isInverse;
        this.setState({
            imgsArrangeArr
        });
    }


    /**
     * componentDidMount(dom)
     * 
     */
    componentDidMount() {

        /** */
        let stageDOM = findDOMNode(this.refs['stage']), // dom
            stageW = stageDOM.scrollWidth,              // 
            stageH = stageDOM.scrollHeight,             // 
            halfStageW = Math.ceil(stageW / 2),         // 
            halfStageH = Math.ceil(stageH / 2);         // 

        /** imgFigureimgFigureimgFigure0*/
        let imgFigureDOM = findDOMNode(this.refs['imgFigure0']), // 
            imgW = imgFigureDOM.scrollWidth,                     // 
            imgH = imgFigureDOM.scrollHeight,                    // 
            halfImgW = Math.ceil(imgW / 2),                      // 
            halfImgH = Math.ceil(imgH / 2);                      // 

        /** ,  this.Constant */
        this.Constant.centerPos = {
            left: halfStageW - halfImgW,   // left
            top: halfStageH - halfImgH     // top
        };

        /** xy */
        this.Constant.leftSection.x[0] = -halfImgW;                         // 
        this.Constant.leftSection.x[1] = halfStageW - halfImgW * 3;         // left1.5
        this.Constant.leftSection.y[0] = -halfImgH;                         // 
        this.Constant.leftSection.y[1] = stageH - halfImgH;                 // 
        /** xy*/
        this.Constant.rightSection.x[0] = halfStageW + halfImgW;            // 
        this.Constant.rightSection.x[1] = stageW - halfImgW;                // 
        this.Constant.rightSection.y[0] =  this.Constant.leftSection.y[0];  // 
        this.Constant.rightSection.y[1] =  this.Constant.leftSection.y[1];  // 
        /** xy */
        this.Constant.topSection.y[0] = -halfImgH;                          // 
        this.Constant.topSection.y[1] = halfStageH - halfImgH * 3;          // 
        this.Constant.topSection.x[0] = halfStageW - imgW;                  // 
        this.Constant.topSection.x[1] = halfStageW;                         // left

        this.rearrange(0); //
    }


    render() {
        let cotrollerUnits = [],
            imgFigure = [];

        imageDatas.forEach((value, index) => {
            if (!this.state.imgsArrangeArr[index]) {
                this.state.imgsArrangeArr[index] = {
                    pos: {
                        left: 0,
                        top: 0
                    },
                    rotate: 0,
                    isInverse: false,
                    isCenter: false
                }
            }
            let commonProps = {
                key: index,
                arrange: this.state.imgsArrangeArr[index],
                inverse: this.inverse.bind(this,index),
                center: this.center.bind(this,index)
            };
            imgFigure.push( <ImgFigure data={value} ref={'imgFigure' + index} {...commonProps}/> );
            cotrollerUnits.push( <ControllerUnit  {...commonProps} />);
        });

        return (
            <section className="stage" ref="stage">
                <section className="img-sec">
                    {imgFigure}
                </section>
                <nav className="controller-nav">
                    {cotrollerUnits}
                </nav>
            </section>
        );
    }
}

export default GalleryByReactApp;


