import React from 'react';
import classNames from 'classnames';

class ImgFigure extends React.Component {

    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    /**
     * imgFigure
     *  
     *  
     *  
     */
    handleClick(e){
        if(this.props.arrange.isCenter){
            this.props.inverse();
        }else {
            this.props.center();
        }
        e.stopPropagation();
        e.preventDefault();
    }

    /**
     * 
     * 1. position
     * 2. transform:rotate
     * 3. z-index
     */
    getImgeStyle(){
        let styleObj = {};

        /* props, */
        if (this.props.arrange.pos) {
            styleObj = this.props.arrange.pos;
        }

        /*  */
        if(this.props.arrange.rotate){
            let prefixArr =  ['MozTransform','MsTransform','WebkitTransform','transform'];
            prefixArr.forEach( value => {
                styleObj[value] = `rotate(${this.props.arrange.rotate}deg)`;
            });
        }

        /* z-index, */
        if(this.props.arrange.isCenter){
            styleObj.zIndex = 11;
        }
        return styleObj;
    }


    render() {
        let styleObj = this.getImgeStyle();

        let figureClass = classNames({
            'img-figure': true,
            'is-inverse': this.props.arrange.isInverse   // 
        });

        let data = this.props.data;

        return (
            <figure className={figureClass}
                    style={styleObj}
                    onClick={this.handleClick}>

                <img src={data.imageUrl} alt={data.title}/>
                <figcaption>
                    <h2 className="img-title">{data.title}</h2>
                    <div className="img-back" onClick={this.handleClick}>
                        <p> {data.desc} </p>
                    </div>
                </figcaption>
            </figure>
        );
    }
}

ImgFigure.propTypes = {
    data: React.PropTypes.object.isRequired,      // 
    arrange: React.PropTypes.object.isRequired,   // ,
    inverse: React.PropTypes.func.isRequired,     // 
    center: React.PropTypes.func.isRequired       // 
};

export default ImgFigure;


/**
   1. data
     {
       "fileName": "1.jpg",
       "title": "Heaven of time",
       "desc": "Here he comes Here comes Speed Racer."
     }
   2. arrange
      {
         pos:{ left:0, top:0}
         rotate: 0          //
         isInverse false  // 
         isCenter: false   //
      }


 **/


