import React from 'react';
import classNames from 'classnames';

/**
 * 
 */
class ControllerUnit extends React.Component {

    /**
     * ControllerUnit
     *  
     *  
     *  
     */
    handleClick(e){
        if(this.props.arrange.isCenter){
            this.props.inverse();
        }else {
            this.props.center();
        }
        e.stopPropagation();
        e.preventDefault();
    }

    render(){

        let spanClass = classNames({
            'controller-unit': true,
            'is-center': this.props.arrange.isCenter,   // 
            'is-inverse': this.props.arrange.isInverse  // 
        });

        return (
            <span className={spanClass} onClick={this.handleClick.bind(this)}> </span>
        );
    }
}
ControllerUnit.propTypes = {
    arrange: React.PropTypes.object.isRequired,   // ,
    inverse: React.PropTypes.func.isRequired,     // 
    center: React.PropTypes.func.isRequired       // 
};

export default ControllerUnit;

/**
 1. arrange
 {
    pos:{ left:0, top:0}
    rotate: 0          //
    isInverse false  // 
    isCenter: false   //
 }


 **/


